<template>
  <div class="bump64">
    <div class="row">
      <div class="col-xs-12">
        <div class="sectionHeader">
          <div style="text-align:right"><router-link to="LeaderBoard" >LEADERBOARD</router-link> | DASHBOARD |  <router-link to="Profile" >PROFILE</router-link></div>
          <h1>DASHBOARD</h1>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <p>The dashboard's purpose will be for drill downs on your specific statistics.</p>
      </div>
     </div>
  </div>
</template>

<script>

import {api} from '../../utils/api.js'
import {storage} from '../../utils/storage.js'

export default {
  name: 'DashBoard',
  mounted : function () {
    if(storage.getValue('isGood') === 'no'){
      this.$router.push('/');
    }
  }
}

</script>

